import React, { useState, useEffect } from "react";
import ReactMarkdown from "react-markdown";
import styles from "./startpage.module.css";
import AboutSection from "./AboutSection";
import AcademicsSection from "./AcademicsSection";
import StackSection from "./StackSection";
import ExperienceSection from "./ExperienceSection";
import ProjectSection from "./ProjectSection";

function StartPage() {
  const [activeSection, setActiveSection] = useState("about");
  const [markdownContent, setMarkdownContent] = useState("");

  useEffect(() => {
    // Fetch Markdown content dynamically for the selected section
    fetch(`/markdowns/${activeSection}.md`)
      .then((response) => {
        if (!response.ok) {
          throw new Error(`Could not fetch ${activeSection}.md`);
        }
        return response.text();
      })
      .then((text) => setMarkdownContent(text))
      .catch((err) => console.error("Error loading markdown file:", err));
  }, [activeSection]);

  const renderSection = () => {
    switch (activeSection) {
      case "about":
        return <AboutSection content={markdownContent} />;
      case "academics":
        return <AcademicsSection content={markdownContent} />;
      case "stack":
        return <StackSection content={markdownContent} />;
      case "experience":
        return <ExperienceSection content={markdownContent} />;
      case "projects":
        return <ProjectSection content={markdownContent} />;
      default:
        return null;
    }
  };

  return (
    <div className={styles.otrmkdwn}>
      <h1>Shahroz Jan</h1>
      <nav>
        {["about", "academics", "stack", "experience","projects"].map((section) => (
          <button
            key={section}
            onClick={() => setActiveSection(section)}
            className={activeSection === section ? styles.activeButton : ""}
          >
            {section.toUpperCase()}
          </button>
        ))}
      </nav>
      <div className={styles.mkdwn}>{renderSection()}</div>
    </div>
  );
}

export default StartPage;
